import React, { useState } from "react";
import { Card, Tag, Modal } from "antd";
import "./projet.scss";
import argent from "../../assets/images/Argent_bank_home.png";
import kasa from "../../assets/images/Kasa_appt_2.png";
import ohmyfood from "../../assets/images/OhMyFood_home.png";
import debuggevent from "../../assets/images/724event.png";

const { Meta } = Card;

const stackColors = {
  react: "#61dafb",
  redux: "#764abc",
  router: "#eb4034",
  swagger: "#85ea2d",
  html: "#e44d26",
  css: "#000080",
  sass: "#cc6699",
  "font-awesome": "#3399cc",
};

const Project = ({
  title,
  image,
  date,
  stacks,
  githubRepo,
  liveSite,
  description,
  onClick,
}) => (
  <Card
    hoverable
    style={{ width: "500px" }} // Ajustement pour la largeur maximale
    cover={<img alt={title} src={image} />}
    onClick={onClick}
  >
    <Meta title={title} description={`Priode: ${date}`} />
    <div className="technologies">
      {stacks.map((stack, index) => (
        <Tag
          key={index}
          style={{
            backgroundColor: stackColors[stack.toLowerCase()],
            color: "#ffffff",
          }}
          className={`stack-${stack.toLowerCase()}`}
        >
          {stack}
        </Tag>
      ))}
    </div>
    <div className="project-link repoGit-link">
      <a href={githubRepo} target="_blank" rel="noopener noreferrer">
        Repository
      </a>
      {liveSite && (
        <div className="project-link liveSite-link">
          <a href={liveSite} target="_blank" rel="noopener noreferrer">
            Live Site
          </a>
        </div>
      )}
    </div>
  </Card>
);

const projectsData = [
  {
    title: "Argent-Bank",
    image: argent,
    date: "02-2024",
    stacks: ["React", "Redux", "Router", "CSS", "Swagger"],
    githubRepo: "https://github.com/vYna-Yvan/Argent-Bank-React",
    liveSite: "",
    description: `Une application bancaire en ligne permettant de grer vos finances. 
    J'ai complt la cration d'une application bancaire en ligne permettant de grer vos finances. 
    J'ai utilis React pour son dveloppement, intgr Redux pour sa gestion, et implment les fonctionnalits de connexion, 
    dconnexion et visualisation du profil. J'ai suivi les bonnes pratiques de dveloppement et utilis les routes API dj disponibles.`,
  },
  {
    title: "724Events Debug",
    image: debuggevent,
    date: "01-2004",
    stacks: ["React", "SASS"],
    githubRepo: "https://github.com/vYna-Yvan/724events",
    liveSite: "",
    description: `Une application de gestion d'vnements pour aider les organisateurs  suivre les participants.
    En tant que dveloppeur front-end freelance, j'ai t sollicit par l'agence vnementielle 724events pour finaliser la nouvelle version de leur site vitrine.
     Le site avait dj t complt, mais des bugs persistaient, entravant son bon fonctionnement pour les visiteurs. 
     Jean-Baptiste, le directeur Marketing de l'agence, m'a envoy le code du site avec une liste de tests unitaires et fonctionnels  effectuer pour dtecter les erreurs.
      Aprs avoir accept la mission, j'ai examin le code et suivi les instructions de Jean-Baptiste pour identifier et corriger les problmes. 
      J'ai galement complt le cahier de recette, en ajoutant tous les scnarios ncessaires pour vrifier que le site fonctionne correctement.
      Maintenant que j'ai termin les tests et les corrections ncessaires, le site devrait tre prt  tre publi sans aucun bug gnant.`,
  },
  {
    title: "Kasa",
    image: kasa,
    date: "12-2023",
    stacks: ["React", "Router", "CSS"],
    githubRepo: "https://github.com/username/kasa-repo",
    liveSite: "https://vyna-yvan.github.io/kasa/",
    description: `Une application de location d'appartements pour des sjours courts.
    J'ai rnov le site de Kasa, une entreprise de location d'appartements. 
    J'ai utilis JavaScript pour le ct technique et React pour le design. 
    Avec les directives de Laura et les maquettes de Paul, j'ai cr une nouvelle interface utilisateur. 
    Aprs avoir ajout les annonces de logements, j'ai termin le projet avec succs.`,
  },
  {
    title: "OhMyFood",
    image: ohmyfood,
    date: "09-2023",
    stacks: ["HTML", "SASS", "Font-Awesome"],
    githubRepo: "https://github.com/vYna-Yvan/OhMyFood",
    liveSite: "https://vyna-yvan.github.io/OhMyFood/",
    description: `Une application de commande de repas en ligne avec des animations attractives.
      J'ai travaill sur un projet pour Ohmyfood en tant que dveloppeur junior. 
      Notre mission tait de crer un site pour afficher les menus des restaurants. 
      Les clients pouvaient composer leur propre menu. Aprs avoir convaincu 4 restaurateurs de participer, 
      j'ai dvelopp le site en utilisant les maquettes fournies. Maintenant, le projet est termin.`,
  },
];

const ProjectCard = () => {
  const [modalVisible, setModalVisible] = useState(false);
  const [selectedProject, setSelectedProject] = useState(null);

  const openModal = (project) => {
    setSelectedProject(project);
    setModalVisible(true);
  };

  const closeModal = () => {
    setModalVisible(false);
  };

  return (
    <div className="card-list">
      {projectsData.map((project, index) => (
        <Project
          key={index}
          title={project.title}
          image={project.image}
          date={project.date}
          stacks={project.stacks}
          githubRepo={project.githubRepo}
          liveSite={project.liveSite}
          description={project.description}
          onClick={() => openModal(project)}
        />
      ))}
      <Modal
        title={selectedProject && selectedProject.title}
        open={modalVisible}
        onCancel={closeModal}
        footer={null}
        className="project-modal"
      >
        {selectedProject && (
          <div>
            <p className="modal-description">{selectedProject.description}</p>
            <div className="technologies">
              {selectedProject.stacks.map((stack, index) => (
                <Tag
                  key={index}
                  style={{
                    backgroundColor: stackColors[stack.toLowerCase()],
                    color: "#ffffff",
                  }}
                  className={`stack-${stack.toLowerCase()}`}
                >
                  {stack}
                </Tag>
              ))}
            </div>
            <p>Priode: {selectedProject.date}</p>
            <p>
              <a
                href={selectedProject.githubRepo}
                target="_blank"
                rel="noopener noreferrer"
              >
                GitHub Repo
              </a>
            </p>
            {selectedProject.liveSite && (
              <p>
                <a
                  href={selectedProject.liveSite}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  Live Site
                </a>
              </p>
            )}
          </div>
        )}
      </Modal>
    </div>
  );
};

export default ProjectCard;
